<template>
  <div class="transaction-details-page">
    <el-form :inline="true" :model="formData">
      <el-form-item label="交易时间：">
        <el-date-picker
          v-model="formData.dateList"
          type="daterange"
          range-separator="~"
          format="YYYY-MM-DD"
          value-format="YYYY-MM-DD"
          start-placeholder="开始时间"
          end-placeholder="结束时间"
        />
      </el-form-item>

      <el-form-item label="订单号：">
        <el-input v-model="formData.orderNumber" placeholder="请输入订单号" />
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchHandler">搜索</el-button>
      </el-form-item>
    </el-form>

    <el-card class="balance-composition" shadow="never">
      <h6>余额组成</h6>
      <div class="container">
        <div class="item">
          <div class="title">余额</div>
          <div class="value">{{ toMoney(infoData.balance) }}</div>
        </div>
        <div class="item available-balance">
          <div class="title">可用余额</div>
          <div class="value">{{ toMoney(infoData.availableBalance) }}</div>
          <el-link
            v-if="!['ec-line'].includes(systemName)"
            type="primary"
            @click="insufficientBalanceDialogRef.toggle()"
            >提醒设置
          </el-link>
        </div>
        <div class="item">
          <div class="title">保证金</div>
          <div class="value">{{ toMoney(infoData.ensureBalance) }}</div>
          <el-link type="primary" @click="bondDetailsDrawerRef.toggle()"
            >查看明细
          </el-link>
        </div>
      </div>
    </el-card>

    <div class="table-container">
      <el-table :data="pageData.list" height="calc(100vh - 448px)">
        <el-table-column prop="id" label="流水号" width="220" />
        <el-table-column prop="createdTime" label="交易时间" width="200" />
        <el-table-column prop="tradeTypeName" label="交易类型" width="140">
          <template #header>
            交易类型
            <el-tooltip
              class="box-item"
              effect="dark"
              content="交易类型分为：消费-订单消耗；充值-代理商充值；退款-订单退款；调拨-现金券下发；返佣-因客户订单产生的返佣；"
              placement="top"
            >
              <el-icon :size="16" color="#b1b3b8">
                <QuestionFilled />
              </el-icon>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column prop="cashOutNo" label="订单号/流水号" width="220">
          <template #header>
            订单号/流水号
            <el-tooltip
              class="box-item"
              effect="dark"
              content="余额变动关联的交易订单号/流水号"
              placement="top"
            >
              <el-icon :size="16" color="#b1b3b8">
                <QuestionFilled />
              </el-icon>
            </el-tooltip>
          </template>
          <template #default="{ row }">
            <el-link
              v-if="allowSkipTradeType.includes(row.tradeType)"
              @click="handleJump(row.cashOutNo)"
              type="primary"
              >{{ row.cashOutNo }}
            </el-link>
            <template v-else>{{ row.cashOutNo }}</template>
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="备注" min-width="250">
          <template #default="{ row }">
            <el-tooltip
              class="box-item"
              effect="dark"
              :content="row.remark"
              placement="top"
            >
              <div class="one-row">{{ row.remark }}</div>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column prop="cashOutTypeName" label="收支类型" width="140" />
        <el-table-column prop="amount" label="金额" width="140" />
        <el-table-column prop="accountBalance" label="余额" width="140" />
        <template #empty>
          <TableEmpty />
        </template>
      </el-table>

      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      ></PagePagination>
    </div>
  </div>

  <InsufficientBalanceDialog
    v-model:alarm-balance="infoData.alarmBalance"
    ref="insufficientBalanceDialogRef"
  />

  <BondDetailsDrawer
    :ensure-balance="infoData.ensureBalance"
    ref="bondDetailsDrawerRef"
  />
</template>
<script setup lang="ts">
import { reactive, ref } from "vue";
import { QuestionFilled } from "@element-plus/icons-vue";
import InsufficientBalanceDialog from "@views/financial-center/fund-management/income-expenditure/InsufficientBalanceDialog.vue";
import BondDetailsDrawer from "@views/financial-center/fund-management/income-expenditure/BondDetailsDrawer.vue";
import {
  assetsInfoApi,
  getCashOutListForPageApi,
} from "@api/financial-center-api";
import { LocalStorage, toMoney, usePage } from "jz-tool-lib";
import { allowSkipTradeType } from "@views/financial-center/fund-management/income-expenditure/compose";
import { useRouter } from "vue-router";
import { systemName } from "@assets/common";

const router = useRouter();
const formData = reactive({
  dateList: [],
  orderNumber: "",
});
const infoData = reactive({
  alarmBalance: "",
  availableBalance: 0,
  balance: 0,
  ensureBalance: 0,
});
const agentId = LocalStorage.getItem("bd_agentId");
const insufficientBalanceDialogRef = ref();
const bondDetailsDrawerRef = ref();

const searchHandler = () => {
  console.log(formData);
  resetPageNumber();
  getList();
};

function assetsInfo() {
  assetsInfoApi({
    agencyId: agentId,
  }).then((res) => {
    console.log(res, "line 143");
    infoData.alarmBalance = res?.data?.alarmBalance;
    infoData.availableBalance = res?.data?.availableBalance || 0;
    infoData.balance = res?.data?.balance || 0;
    infoData.ensureBalance = res?.data?.ensureBalance || 0;
  });
}

function handleJump(orderNumber: string) {
  localStorage.setItem("selectedOrderNumber", orderNumber);
  router.push({
    name: "financialCenter.billManagement.dailyStatement",
  });
}

function getDateRange(dateRange: string[]) {
  const [start, end] = dateRange || [];
  return [start ? `${start} 00:00:00` : "", end ? `${end} 23:59:59` : ""];
}

const { pageNumberChange, pageSizeChange, pageData, getList, resetPageNumber } =
  usePage({
    listApi: getCashOutListForPageApi,
    props: {
      pageNumber: "page",
      pageSize: "pageSize",
    },
    searchParams() {
      const [startTime, endTime] = getDateRange(formData.dateList);
      return {
        agencyId: agentId,
        orderNo: formData.orderNumber,
        startTime,
        endTime,
      };
    },
    notExecute: true,
  });

assetsInfo();
getList();
</script>

<style scoped lang="scss">
.balance-composition {
  h6 {
    color: #242934;
    font-size: 18px;
  }

  .container {
    margin-top: 20px;
    display: flex;
    column-gap: 80px;

    .item {
      font-size: 15px;
      color: #606266;
    }

    .title {
      margin-bottom: 2px;
    }

    .available-balance {
      position: relative;

      &::after {
        content: "+";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: -45px;
      }

      &::before {
        content: "=";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: -45px;
      }
    }
  }
}

.one-row {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
</style>
