<script setup lang="ts">
import { computed, ref } from "vue";
import { alarmBalanceSettingApi } from "@api/financial-center-api";
import { ElMessage } from "element-plus";
import { environment } from "@assets/common";
import { LocalStorage, testMoney } from "jz-tool-lib";
import QrCodeImageDemo from "@assets/images/qrcode_wx_demo.jpg"; // demo 扫码用
import QrCodeImageVip from "@assets/images/qrcode_wx_prod.jpg"; // beta、线上扫码用
const props = defineProps<{
  alarmBalance: number | string;
}>();

const emit = defineEmits<{
  (e: "update:alarmBalance", val: number): void;
}>();
const srcList = ref([
  ["demo"].includes(environment()) ? QrCodeImageDemo : QrCodeImageVip,
]);
const visible = ref(false);
const agentId = LocalStorage.getItem("bd_agentId");
const threshold = computed({
  get() {
    return props.alarmBalance;
  },
  set(val) {
    emit("update:alarmBalance", val as number);
  },
});

function handleOk() {
  if (!testMoney(threshold.value)) {
    return ElMessage.warning("请输入正确金额");
  }
  alarmBalanceSettingApi({
    agencyId: agentId,
    alarmBalance: threshold.value,
  }).then((res) => {
    toggle(false);
    ElMessage.success(res?.msg || "设置成功");
  });
}

function toggle(val = true) {
  visible.value = val;
}

defineExpose({ toggle });
</script>

<template>
  <el-dialog
    v-model="visible"
    title="余额不足提醒"
    class="custom-dialog"
    :width="500"
  >
    <el-form>
      <el-form-item label="阈值设置：">
        <el-input-number
          class="ein"
          v-model="threshold"
          placeholder="请输入阈值"
          :controls="false"
          :min="0"
        />
        <p class="tip">阈值为余额预警额度，余额低于阈值时将提醒您进行充值。</p>
      </el-form-item>

      <el-form-item label="扫码绑定：">
        <el-image
          style="width: 150px; height: 150px"
          :src="srcList[0]"
          :zoom-rate="1.2"
          :max-scale="7"
          :min-scale="0.2"
          :preview-src-list="srcList"
          :initial-index="4"
          fit="cover"
        />
        <p class="tip">
          使用微信扫码进入公众号-支持中心绑定账号，绑定代理商账号，当余额低于闽值时将通过微信公众号通知管理员。
        </p>
      </el-form-item>
    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" @click="handleOk">确定</el-button>
        <el-button @click="visible = false">取消</el-button>
      </span>
    </template>
  </el-dialog>
</template>

<style scoped lang="scss">
.ein {
  :deep(.el-input__inner) {
    text-align: left;
  }
}
.tip {
  line-height: 1.3;
  margin-top: 8px;
  color: #9ca6b9;
}
</style>
