<script setup lang="ts">
import { ref } from "vue";
import { LocalStorage, usePage } from "jz-tool-lib";
import { getFrozenDetailListForPageApi } from "@api/financial-center-api";
import { useRouter } from "vue-router";

const props = defineProps<{
  ensureBalance: number;
}>();

const visible = ref(false);
const router = useRouter();

function toggle(val = true) {
  visible.value = val;
  if (val) {
    getList();
  }
}

const agentId = LocalStorage.getItem("bd_agentId");
const { pageNumberChange, pageSizeChange, pageData, getList } = usePage({
  listApi: getFrozenDetailListForPageApi,
  props: {
    pageNumber: "page",
    pageSize: "pageSize",
  },
  searchParams() {
    return {
      agencyId: agentId,
      // 以下两个参数是后端预写，原型里不需要
      // startTime,
      // endTime,
    };
  },
  notExecute: true,
});

function handleJump(orderNumber: string) {
  localStorage.setItem("selectedOrderNumber", orderNumber);
  router.push({
    name: "financialCenter.billManagement.dailyStatement",
  });
}

defineExpose({ toggle });
</script>

<template>
  <el-drawer
    v-model="visible"
    title="保证金明细"
    size="1200"
    class="custom-drawer"
  >
    <p>当前保证金总额：{{ props.ensureBalance }}</p>
    <div class="table-container">
      <el-table :data="pageData.list" height="calc(100vh - 225px)">
        <el-table-column prop="createdTime" label="时间" width="220" />
        <el-table-column prop="orderNo" label="订单号\交易号" width="220">
          <template #default="{ row }">
            <el-link @click="handleJump(row.orderNo)" type="primary"
              >{{ row.orderNo }}
            </el-link>
          </template>
        </el-table-column>
        <el-table-column prop="companyName" label="使用账户" width="180" />
        <el-table-column prop="productName" label="产品名称" width="180" />
        <el-table-column prop="typeText" label="类型" width="100" />
        <el-table-column prop="sourceText" label="来源" width="180" />
        <el-table-column prop="amount" label="变动金额" width="150" />
        <el-table-column prop="frozenAmount" label="总保证金" width="150" />
        <template #empty>
          <TableEmpty />
        </template>
      </el-table>

      <PagePagination
        :pageTotal="pageData.pageTotal"
        :pageSize="pageData.pageSize"
        :pageNumber="pageData.pageNumber"
        @emitPageSizeChange="pageSizeChange"
        @emitPageNumberChange="pageNumberChange"
      ></PagePagination>
    </div>
  </el-drawer>
</template>

<style scoped lang="scss"></style>
